
.react-player-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  height: 100%;
  min-height: 100%;
  max-height: 100%;
}
.react-player {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.react-player iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  border: none;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.react-player::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(black, transparent, transparent, transparent, transparent, black);
  content: "";
  overflow: hidden;
  pointer-events: none;
}
@media (min-aspect-ratio: 16/9) {
  .react-player iframe {
    height: 56.25vw;
 }
}
@media (max-aspect-ratio: 16/9) {
  .react-player iframe {
    width: 177.78vh;
 }
}
